import React, { useContext, useState, useEffect } from "react";

import { useTranslationContext } from "../../../Providers/TranslationProvider";

import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, FormControlLabel, Grid, IconButton, InputLabel, MenuItem, Radio, RadioGroup, FormControlLabel as RadioFormControlLabel, Switch, TextField, Typography, Paper, Box } from "@mui/material";
import { Edit as EditIcon, Delete as DeleteIcon } from "@mui/icons-material";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";
import { siteStore } from "../../../../core/stores/siteStore";
import { useParams } from "react-router-dom";
import { DndContext, closestCenter, MouseSensor, TouchSensor, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, sortableKeyboardCoordinates, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { ArrowRight } from "@mui/icons-material";

export default function LinkSettings({ hostname }) {

    const params = useParams();
    hostname = hostname || params.hostname
    const { t } = useTranslationContext("options", { keyPrefix: "settings.site.linkSettings" });
    const { linkSettings, linkOpenOptions, setSiteConfig } = siteStore((state) => ({
        linkSettings: state.sites.find(site => site.hostname === hostname)?.linkSettings,
        linkOpenOptions: state.sites.find(site => site.hostname === hostname)?.linkOpenOptions || [],
        setSiteConfig: state.setSiteConfig
    }));

    const [open, setOpen] = useState(false);
    const [regex, setRegex] = useState("");
    const [target, setTarget] = useState("_self");
    const [localLinkOpenOptions, setLocalLinkOpenOptions] = useState([]);

    useEffect(() => {
        setLocalLinkOpenOptions(linkOpenOptions);
    }, [linkOpenOptions]);

    const handleChange = async (event) => {
        await setSiteConfig(hostname, { linkSettings: event.target.checked });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkSettings: event.target.checked } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkSettings', value: event.target.checked });
        }
    }

    const handleConfigureOpen = () => {
        setOpen(true);
    };

    const handleConfigureClose = () => {
        setOpen(false);
    };

    const handleSave = async () => {
        const newOptions = [...localLinkOpenOptions.filter(option => option.regex !== regex), { id: crypto.randomUUID(), regex, target, order: localLinkOpenOptions.length }];
        setLocalLinkOpenOptions(newOptions);
        await setSiteConfig(hostname, { linkOpenOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkOpenOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkOpenOptions', value: newOptions });
        }
        setRegex("");
        setTarget("_self");
    };

    const handleDelete = async (id) => {
        const newOptions = localLinkOpenOptions.filter(option => option.id !== id);
        setLocalLinkOpenOptions(newOptions);
        await setSiteConfig(hostname, { linkOpenOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkOpenOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkOpenOptions', value: newOptions });
        }
    };

    const handleEdit = (id) => {
        const option = localLinkOpenOptions.find(option => option.id === id);
        setRegex(option.regex);
        setTarget(option.target);
    };

    const handleDragEnd = async (event) => {
        const { active, over } = event;
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = localLinkOpenOptions.findIndex(option => option.id === active.id);
        const newIndex = localLinkOpenOptions.findIndex(option => option.id === over.id);
        const newOptions = arrayMove(localLinkOpenOptions, oldIndex, newIndex);
        setLocalLinkOpenOptions(newOptions);
        await setSiteConfig(hostname, { linkOpenOptions: newOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { linkOpenOptions: newOptions } });
        const openTabs = await chrome.tabs.query({ url: [`https://${hostname}/*`, `http://${hostname}/*`] });
        if (openTabs.length === 0) return;
        for (const tab of openTabs) {
            sendTabMessage(tab.id, 'update_site_config', { key: 'linkOpenOptions', value: newOptions });
        }

    };

    function SortableItem({ id, children }) {
        const { attributes, listeners, setNodeRef, transform, transition } = useSortable({ id });

        const style = {
            transform: CSS.Transform.toString(transform),
            transition,
            cursor: 'move' // 
        };

        return (
            <div ref={setNodeRef} style={style} {...attributes} {...listeners}>
                {children}
            </div>
        );
    }

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    return (
        <Grid container alignItems="center" >
            <Grid item xs={12} md={8}>
                <Typography variant="h3" mb={1}>{t("label")}</Typography>
                <Typography variant="body1" color='text.secondary'>{t("description")} </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
                <FormControlLabel control={<Switch color="error" checked={linkSettings === true}
                    onChange={handleChange} />}
                />
                {linkSettings && (
                    <Button size="small" endIcon={<ArrowRight />} variant="text" color="error" onClick={handleConfigureOpen}>
                        {t('configure')}
                    </Button>

                )}
                <Dialog
                    open={open}
                    onClose={(event, reason) => { if (reason !== 'backdropClick') { handleConfigureClose } }}
                    maxWidth="md"
                    fullWidth
                >
                    <DialogTitle variant="h3">{t("configure")}</DialogTitle>
                    <DialogContent>
                        {localLinkOpenOptions.length > 0 && (
                            <Box border={1} borderColor="grey.300" borderRadius={2} p={2} mb={2}>
                                <Typography variant="body2" color="textSecondary" gutterBottom>{t("listTips")}</Typography>
                                <Grid container alignItems="center" spacing={2} sx={{ borderBottom: '1px solid grey', paddingBottom: "10px" }}>
                                    <Grid item xs={4}>
                                        <Typography variant="h4">{t("linkMatchContent")}</Typography>
                                    </Grid>
                                    <Grid item xs={4}>
                                        <Typography variant="h4">{t("linkOpenMethod")}</Typography>
                                    </Grid>
                                    <Grid item xs={2}>
                                        <Typography variant="h4">{t("edit")}</Typography>
                                    </Grid>
                                    <Grid item xs={2}>
                                        <Typography variant="h4">{t("delete")}</Typography>
                                    </Grid>
                                </Grid>

                                <DndContext
                                    collisionDetection={closestCenter}
                                    onDragEnd={handleDragEnd}
                                    sensors={sensors}
                                >
                                    <SortableContext
                                        items={localLinkOpenOptions}
                                        strategy={verticalListSortingStrategy}
                                    >
                                        {localLinkOpenOptions.map((option) => (
                                            <SortableItem key={option.id} id={option.id}>
                                                <Grid container alignItems="center" spacing={1}>
                                                    <Grid item xs={4}>
                                                        <Typography>{option.regex}</Typography>
                                                    </Grid>
                                                    <Grid item xs={4}>
                                                        <Typography>
                                                            {option.target === "_self" && t("target_self")}
                                                            {option.target === "_blank" && t("target_blank")}
                                                            {option.target === "_parent" && t("target_parent")}
                                                            {option.target === "_top" && t("target_top")}
                                                        </Typography>
                                                    </Grid>
                                                    <Grid item xs={2}>
                                                        <IconButton color="primary" onClick={() => handleEdit(option.id)}>
                                                            <EditIcon />
                                                        </IconButton>
                                                    </Grid>
                                                    <Grid item xs={2}>
                                                        <IconButton color="error" onClick={() => handleDelete(option.id)}>
                                                            <DeleteIcon />
                                                        </IconButton>
                                                    </Grid>
                                                </Grid>
                                            </SortableItem>
                                        ))}
                                    </SortableContext>
                                </DndContext>

                            </Box>
                        )}
                        <Paper variant="outlined" style={{ padding: '16px', marginTop: '16px' }}>
                            <Typography variant="h4" gutterBottom>{t("linkMatchContent")}</Typography>
                            <Typography variant="body2" color="textSecondary" gutterBottom>{t("linkMatchContentTips")}</Typography>
                            <FormControl fullWidth margin="normal">
                                <TextField
                                    // label={t("linkMatchContent")}
                                    value={regex}
                                    autoComplete="off"
                                    onChange={(e) => setRegex(e.target.value)}
                                    variant="standard"
                                />
                            </FormControl>
                            <Typography variant="h4">{t("linkOpenMethod")}</Typography>
                            <FormControl component="fieldset" fullWidth margin="normal">

                                <RadioGroup
                                    row
                                    value={target}
                                    onChange={(e) => setTarget(e.target.value)}
                                >
                                    <RadioFormControlLabel value="_self" control={<Radio />} label={t("target_self")} />
                                    <RadioFormControlLabel value="_blank" control={<Radio />} label={t("target_blank")} />
                                    <RadioFormControlLabel value="_parent" control={<Radio />} label={t("target_parent")} />
                                    <RadioFormControlLabel value="_top" control={<Radio />} label={t("target_top")} />
                                </RadioGroup>
                            </FormControl>
                            <Button onClick={handleSave} color="error" variant="contained" style={{ marginTop: '16px' }} disabled={!regex}>
                                {t("save")}
                            </Button>
                        </Paper>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleConfigureClose} color="error">
                            {t("close")}
                        </Button>
                    </DialogActions>
                </Dialog>
            </Grid>
        </Grid>
    )
}